.products{
    display: flex;
    flex-direction: row;
    align-self: center;
}

.scrollLeft{
    height: 40px;
    width: 40px;
    background: transparent;
    border: none;
    align-items: center;
    align-self: center;
    margin-left: 30px;
    cursor: pointer;

}

.scrollRight{
    height: 40px;
    width: 40px;
    background: transparent;
    border: none;
    align-items: center;
    align-self: center;
    margin-left: 30px;
    cursor: pointer;

}

.icon{
    font-size: 25px;
    color: #8c8c8c
}

.parent{
    width: 90%;
    position: relative;
}

.allProduct{
    display: flex;
    overflow: auto;
    flex-direction: row;
    flex-flow: row;
    scroll-behavior: smooth;
    position: relative;

}

.allProduct::-webkit-scrollbar{
    -webkit-appearance: none;
}