.cart {
  display: flex;
}

.proceed {
  width: 20%;
  margin: 30px 10px;
  height: 150px;
  background-color: #e6e6e6;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  padding: 15px;
}

.totAmount {
  font-size: 20px;
  color: var(--grayShade_1);
  margin-top: 10px;
}

.amount {
  color: var(--keyColor);
  font-size: 22px;
  font-weight: 500;
}

.proceedBtn {
  margin-top: 10px;
  height: 40px;
  border: none;
  border-radius: 10px;
  background-color: #ffd52d;
  font-family: "Lexend", sans-serif;
  font-size: 15px;
  cursor: pointer;
}
