.productForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pfTop {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.imgCont {
  margin: 0px 100px;
  height: 250px;
  width: 250px;
  background-color: hsl(0, 0%, 85%);
  border-radius: 15px;
  /* align-items: center; */
  display: flex;
  justify-content: center;
}

.imgCont {
  padding: 10px;
}

.imgCont img {
  height: 200px;
}

.pfBottom {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  justify-content: center;
}

.pfBottom label {
  font-size: 20px;
  color: var(--grayShade_1);
  margin-bottom: 10px;
}

.pfBottom textarea {
  margin-bottom: 20px;
}

.pfBottom select {
  width: 126px;
  height: 45px;
  border-radius: 15px;
  margin-bottom: 20px;
}
