.login {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.subLogin {
  display: flex;
  flex-direction: column;
  height: 450px;
  width: 350px;
  border-radius: 20px;
  background-color: #e6e6e6;
  /* justify-content: center; */
  align-items: center;
  align-self: center;
  padding: 10px 20px;
  margin-top: 100px;
}

.subLogin span {
  font-size: 20px;
}

.loginForm {
  display: flex;
  flex-direction: column;
}

.cardI {
  margin-top: 30px;
  width: 326px;
  border-radius: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.cardI label {
  margin-left: 20px;
  color: var(--grayShade_1);
  font-weight: 400;

  font-size: 20px;
}

.cardI input {
  background-color: transparent;
  outline: none;
  color: var(--grayShade_2);
  font-weight: 400;
  border: none;
  margin-left: 5px;
  border-bottom: 2px solid var(--grayShade_2);
}

.loginForm button {
  margin-top: 50px;
  width: 200px;
  height: 35px;
  border-radius: 12px;
  border: none;
  outline: none;
  background-color: var(--keyColor);
  color: #eeeeee;
  font-size: 20px;
  font-weight: 500;
  align-self: center;
}

.login span:last-child {
  color: #b4b4b4;
  font-weight: 400;
  font-size: 12px;
  cursor: pointer;
  margin-top: 20px;
  transition: 0.5s;
}
