.cartProduct {
  display: flex;
  flex-direction: row;
  height: 250px;
  width: 70%;
  margin: 30px;
  background-color: #e6e6e6;
  border-radius: 20px;
}

.cpLeft {
  height: 220px;
  width: 220px;
  background-color: hsl(0, 0%, 85%);
  border-radius: 15px;
  align-self: center;
  margin-left: 15px;
}

.cpLeft .prodImg {
  height: 220px;
}

.cpRight {
  height: 220px;
  align-self: center;
  margin-left: 15px;
  display: flex;
  flex-direction: column;
}

.cpProdName {
  font-size: 25px;
  font-weight: 500;
  color: var(--grayShade_1);
}
.cpProdDesc {
  margin-top: 10px;
  color: var(--grayShade_2);
  padding-right: 10px;
}

.cpProdPrice {
  margin-top: 10px;
}

.cpProdQuant {
  margin-top: 10px;
  height: 30px;
  width: 65px;
  background-color: hsl(0, 0%, 85%);
  justify-content: space-between;
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 10px;
}

.cpProdQuant .icon{
  font-size: 15px;
  color: var(--grayShade_2);
  cursor: pointer;
}

.totQuant{
  font-size: 20px;
  color: var(--grayShade_1);
}
