.productPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.ppTop {
  display: flex;
  justify-content: space-between;
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 30px;
}

.ppTop span {
  font-size: 30px;
  color: var(--grayShade_2);
  font-weight: 500;
}

.ppTopRight {
  display: flex;
}

.searchBar {
  width: 200px;
  height: 35px;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-right: 10px;
}

.searchBar::before {
  content: "";
  /* top: 0; */
  /* left: 0; */
  position: absolute;
  background-color: #e6e6e6;
  width: 200px;
  height: 35px;
  border-radius: 10px;
  z-index: -1;
}

.searchBar input {
  background-color: transparent;
  border: none;
  margin-left: 10px;
  outline: none;
  font-family: "Lexend", sans-serif;

  color: var(--grayShade_2);
}

.searchBar .icon {
  color: var(--grayShade_2);
  font-size: 14px;
  margin-left: 10px;
}

.filterBtn {
  height: 35px;
  width: 35px;
  border: none;
  background-color: #e6e6e6;
  border-radius: 10px;
  color: var(--grayShade_2);
  font-size: 14px;
  cursor: pointer;
  transition: 0.5s;
}

.filterBtn:hover {
  background-color: var(--grayShade_2);
  color: #e6e6e6;
}
.ppBottom{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 20px;
}

.contName {
  margin-left: 30px;
  font-size: 20px;
  color: var(--grayShade_1);
  font-weight: 500;
  
}

.ppContBottom {
  display: flex;
  flex-direction: row;
  align-self: center;
}

.ppContBottom .holder {
  width: 100%;
  
}




