.singleProducts {
  display: flex;
  flex-direction: row;
  margin: 30px;
}

.spLeft {
  display: flex;
  flex: 6;
}

.splLeft {
  height: 500px;
  width: 450px;
  background-color: #e6e6e6;
  border-radius: 20px;
  align-items: center;
  display: flex;
}

.mainProdImg {
  height: 450px;
  width: 450px;
}

.splRight {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.imgCont {
  height: 200px;
  width: 200px;
  background-color: #e6e6e6;
  margin-bottom: 20px;
  border-radius: 20px;
  cursor: pointer;
}

.secondaryImg-1 {
  height: 200px;
  width: 200px;
  transition: 0.5s;
}

.secondaryImg-2 {
  height: 200px;
  width: 200px;
  transition: 0.5s;
}

.imgCont:hover .secondaryImg-1 {
  transform: rotateZ(5deg);
}

.imgCont:hover .secondaryImg-2 {
  transform: rotateZ(5deg);
}

.spRight {
  flex: 6;
  display: flex;
  flex-direction: column;
}

.prodName {
  font-weight: 600;
  font-size: 30px;
  color: var(--grayShade_1);
}

.prodDesc {
  font-weight: 500;
  color: hsl(0, 0%, 15%);
  margin-top: 15px;
}

.prodRating {
  display: flex;
  margin-top: 10px;
  color: var(--grayShade_1);
}

.stars {
  margin-left: 10px;
}

.starIcon {
  margin-right: 5px;
  color: goldenrod;
}

.prodPrice {
  margin-top: 15px;
}

.price {
  font-size: 30px;
  color: var(--keyColor);
  font-weight: 500;
}

.discount {
  margin-left: 5px;
  text-decoration: line-through;
  color: var(--grayShade_2);
}

.offer {
  margin-left: 15px;
  font-size: 18px;
  color: var(--grayShade_2);
}

.btns {
  margin-top: 15px;
}

.bnBtn {
  width: 40%;
  margin-right: 10px;
  height: 50px;
  border: none;
  font-size: 17px;
  font-family: "Lexend", sans-serif;
  font-weight: 500;
  border-radius: 10px;
  background-color: #ff852d;
  cursor: pointer;
}

.atcBtn {
  width: 40%;
  margin-left: 10px;
  height: 50px;
  border: none;
  font-size: 17px;
  font-family: "Lexend", sans-serif;
  font-weight: 500;
  border-radius: 10px;
  background-color: #ffd52d;
  cursor: pointer;
}

.prodReviews {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
}

.prodReviews input {
  margin-top: 10px;
  height: 40px;
  width: 280px;
  border-radius: 10px;
  border: none;
  outline: none;
  background-color: #e6e6e6;
  padding: 0px 10px;
  font-family: "Lexend", sans-serif;
  color: var(--grayShade_2);
}

.postBtn {
  height: 36px;
  width: 120px;
  margin-top: 10px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  background-color: var(--grayShade_1);
  color: #e6e6e6;
  font-family: "Lexend", sans-serif;
}
