.prodLink{
    text-decoration: none;
    color: inherit;
}

.product{
    height: 410px;
    width: 300px;
    background-color: hsl(0, 0%, 90%);
    border-radius: 20px;
    margin: 30px;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    cursor: pointer;
    transition: .5s;
    min-width: 300px;
    min-height: 410px;
}

.product:hover{
    transform: scale(1.025);
}

.productTop{
    align-self: center;
    display: flex;
}

.heart_icon{
    position: absolute;
    font-size: 20px;
    margin-top: 20px;
    color: rgb(251, 36, 36);
    transition: .5s;
    cursor: pointer;
    margin-left: -25px;
}


.productMid{
    height: 5px;
    border-radius: 20px;
    width: 200px;
    background-color: hsl(0, 0%, 85%);
    align-self: center;
}

.productImg{
    height: 200px;
    width: 200px;
    align-self: center;
}
.productBottom{
    display: flex;
    flex-direction: column;

}

.productDesc{
    /* align-self: center; */
    padding: 0px 20px;
    margin-top: 20px;
}

.productRating{
    display: flex;
    margin-left: 20px;
    margin-top: 10px;
    color: var(--grayShade_1);
}

.stars{
    margin-left: 10px;
}

.starIcon{
    margin-right: 5px;
    color: goldenrod;
}

.productPriceAndOff{
    margin-left: 20px;
    margin-top: 10px;
}

.price{
    font-size: 25px;
    color: var(--keyColor);
    font-weight: 500;
}

.discount{
    margin-left: 5px;
    text-decoration: line-through;
    color:var(--grayShade_2)
}

.offer{
    margin-left: 10px;
    font-size: 18px;
    color:var(--grayShade_2)
}