.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  margin-left: 30px;
  font-size: 20px;
  font-weight: 500;
  color: var(--grayShade_1);
  letter-spacing: 2px;
}

.mid {
  align-content: center;
}

.navList {
  display: flex;
}

.item {
  list-style: none;
  margin: 0px 50px;
  cursor: pointer;
  transition: 0.5s;
  color: hsl(0, 0%, 35%);
  font-weight: 400;
}

.active,
.item:hover {
  color: var(--grayShade_1);
}

.right {
  margin-right: 30px;
}

.navBtn {
  height: 30px;
  width: 100px;
  border-radius: 10px;
  border: 3px solid var(--keyColor);
  background-color: transparent;
  color: var(--grayShade_1);
  font-family: "Lexend", sans-serif;
  cursor: pointer;
  transition: 0.5s;
}

.navBtn .itemLink {
  color: inherit;
  text-decoration: none;
}

.navBtn:hover {
  background-color: var(--keyColor);
}
